<template>
  <div>
    <!-- nav start-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/">Roles Management</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>Roles List</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- nav end-->

    <!-- card start -->
    <el-card>
      <!-- add role button start -->
      <el-row>
        <el-col>
          <el-button type="primary" class="addRole-button" @click="dialogVisible = true">Add Role</el-button>
        </el-col>
      </el-row>
      <!-- add role button end -->

      <!-- role list start -->
      <el-table :data="rolelist" border stripe empty-text="No Data">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 == 0 ? 'bdtop' : '', 'vcenter']"
              :key="item1.id"
              v-for="(item1, i1) in scope.row.children"
            >
              <!-- first level -->
              <el-col :span="5" closable @close="removeRightById(scope.row, item1.id)">
                <el-tag>{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- second and third level -->
              <el-col :span="19">
                <!-- use for render level 2 rights -->
                <el-row
                  :class="[i2 == 0 ? '': 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <!-- level 2 -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- level 3 -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      :key="item3.id"
                      v-for="(item3, i3) in item2.children"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- index -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="Role Name" prop="roleName"></el-table-column>
        <el-table-column label="Role Desc" prop="roleDesc"></el-table-column>
        <el-table-column label="Actions" width="350px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
            >Edit</el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeRoleById(scope.row.id)"
            >Delete</el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
            >Assign Roles</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- role list end -->
    </el-card>
    <!-- card end -->

    <!-- assign rights dialog start-->
    <el-dialog
      title="Assign Rights"
      :visible.sync="setRightsDialogVisible"
      width="50%"
      @close="setRightsDialogClosed"
    >
      <!-- tree -->
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        ref="treeRef"
        default-expand-all
        :default-checked-keys="defKeys"
      ></el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightsDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="allotRights">Confirm</el-button>
      </span>
    </el-dialog>
    <!-- assign rights dialog end-->

    <!-- Add Role dialog start-->
    <el-dialog title="Add User" :visible.sync="dialogVisible" width="30%" @close="addDialogClosed">
      <!-- Content -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="130px">
        <el-form-item label="Role Name" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="Role Description" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- Bottom buttons -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="addRole">Add User</el-button>
      </span>
    </el-dialog>

    <!-- edit user dialog start-->
    <el-dialog
      title="Edit Role"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="editDialogClosed"
    >
      <el-form :model="editFrom" :rules="editFormRules" ref="editFormRef" label-width="140px">
        <el-form-item label="Role Name" prop="roleName">
          <el-input v-model="editFrom.roleName"></el-input>
        </el-form-item>
        <el-form-item label="Role Description">
          <el-input v-model="editFrom.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="editRoleInfo">Edit User</el-button>
      </span>
    </el-dialog>
    <!-- edit user dialog end-->
  </div>
</template>

<script>
export default {
  data() {
    return {
      // all roles data
      rolelist: [],
      // control assign rights visible
      setRightsDialogVisible: false,
      // all rights
      rightsList: [],
      // attributes binding for tree
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // default seleted ids
      defKeys: [],
      // to assign role rights
      roleId: '',
      dialogVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormRules: {
        roleName: [
          {
            required: true,
            message: 'Please input your username',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: 'Username is 3 to 10 characters'
          }
        ],
        roleDesc: []
      },
      // control edit dialog
      editDialogVisible: false,
      // fetched user info object
      editFrom: {},
      // edit form validators
      editFormRules: {
        roleName: [
          {
            required: true,
            message: 'Please input your username',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // get all roles data list
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('Get Roles List Failed')
      }
      this.rolelist = res.data
    },
    // delete right by id
    async removeRightById(role, rightId) {
      // message box to remind if confirm deletion
      const confirmResult = await this.$confirm(
        'Confirm to delete?',
        'Confirm',
        {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('Canceled deletion')
      }

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) {
        return this.$message.info('Cancel failed')
      }
      // not use this, will render the whole page
      // this.getRolesList()
      role.children = res.data
    },
    // show dialog for assign rights
    async showSetRightDialog(role) {
      this.roleId = role.id
      // get all rights list
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200)
        return this.$message.info('Get rights list failed')
      // save all rights to rightslist
      this.rightsList = res.data
      // get level 3 rights
      this.getLeafKeys(role, this.defKeys)
      this.setRightsDialogVisible = true
    },
    // get level 3 rights and saved to default selected
    getLeafKeys(node, arr) {
      if (!node.children) {
        // if current is leaf node(level 3), save to arr
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // listen to the close of the dialog
    setRightsDialogClosed() {
      this.defKeys = []
    },
    // update rights
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        {
          rids: idStr
        }
      )
      if (res.meta.status !== 200)
        return this.$message.error('Assigned rights failed')
      this.$message.success('Assigned rights succeed')
      this.getRolesList()
      this.setRightsDialogVisible = false
    },
    async removeRoleById(id) {
      // confirm deletion
      const confirmResult = await this.$confirm(
        'Do you confirm to delete the role?',
        'Confirm',
        {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }
      ).catch(err => err) // if user canceled, need to catch error

      // if user confirmed, returned a "confirm"
      if (confirmResult !== 'confirm') {
        return this.$message.info('Deletion Canceled')
      }

      // request to delete user
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('Deletion failed')
      }
      this.$message.success('Deletion succeed')
      this.getRolesList()
    },
    addRole() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // add user request
        const { data: res } = await this.$http.post('roles', this.addForm)
        if (res.meta.status != 201) {
          this.$message.error('Add Role Failed')
        }
        this.$message.success('Add Role Success')
        // hide add user dialog
        this.dialogVisible = false
        // gain user list
        this.getRolesList()
      })
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // edit role
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('Fetch Role info failed')
      }
      this.editFrom = res.data
      this.editDialogVisible = true
    },
    editRoleInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.put(
          'roles/' + this.editFrom.roleId,
          {
            roleName: this.editFrom.roleName,
            roleDesc: this.editFrom.roleDesc
          }
        )

        if (res.meta.status !== 200) {
          this.$message.error('Failed update role info')
          return
        }

        this.editDialogVisible = false
        this.getRolesList()
        this.$message.success('Success update role info')
      })
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.addRole-button {
  background-color: #fb5020;
  border-color: #fb5020;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

.el-tag {
  margin: 7px;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>